.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.hide-mobile {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.hide-pc {
  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (max-width: $mobile-width-only) {
    display: block;
  }
}
